import styles from "./index.module.scss";
import React, { useState } from "react";
import ReCAPTCHA from "react-google-recaptcha";
import { Link } from "react-router-dom";

const EmailForm: React.FC = () => {
  const [email, setEmail] = useState("");
  const [senderName, setSenderName] = useState("");
  const [message, setMessage] = useState("");
  const [captchaValue, setCaptchaValue] = useState("");
  const [emailError, setEmailError] = useState("");
  const [messageError, setMessageError] = useState("");
  const [verificationError, setVerificationError] = useState("");

  const validateEmail = (email: string): boolean => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  const handleCaptchaChange = (value: string | null) => {
    setCaptchaValue(value || "");
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setEmailError("");
    setMessageError("");
    setVerificationError("");
    if (!validateEmail(email)) {
      setEmailError("    address@example.com ");
      return;
    }
    if (message.length < 4) {
      setMessageError(
        "  ,    - 4 "
      );
      return;
    }

    if (!captchaValue) {
      setVerificationError(",    !");
      return;
    }

    //    
    console.log(" ...");
    console.log(":", email);
    console.log(" :", senderName);
    console.log(" :", message);
    console.log("reCAPTCHA value:", captchaValue);

    //    
    setEmail("");
    setSenderName("");
    setMessage("");
    setCaptchaValue("");
  };

  return (
    <form onSubmit={handleSubmit} className={styles.form}>
      <div className={styles.input}>
        <input
          className={styles.mail}
          type="email"
          id="email"
          placeholder="email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
        />
        <label htmlFor="email">{emailError}</label>
      </div>

      <div className={styles.input}>
        <input
          type="text"
          placeholder=""
          id="senderName"
          value={senderName}
          onChange={(e) => setSenderName(e.target.value)}
          required
        />
        <label htmlFor="senderName"></label>
      </div>

      <div className={styles.input}>
        <textarea
          className={styles.text}
          id="message"
          placeholder="  "
          value={message}
          onChange={(e) => setMessage(e.target.value)}
          required
        />
        <label htmlFor="message">{messageError}</label>
      </div>
      <div className={styles.ReCAPTCHA}>
        <ReCAPTCHA sitekey=" siteKey" onChange={handleCaptchaChange} />
        <label htmlFor="ReCAPTCHA">{verificationError}</label>
      </div>
      {/*    reCAPTCHA     " ".
        Google.      ,  .
       .       "",   reCAPTCHA (    "reCAPTCHA v2"),         "".
      reCAPTCHA.
      siteKey.         reCAPTCHA   .
 siteKey   ,          .
 ,                    siteKey,  reCAPTCHA.
,       ,  Google reCAPTCHA,      reCAPTCHA   . */}

      <button type="submit" className={styles.button}>
        
      </button>

      <div className={styles.link}>
           ,     
           &nbsp;
        <Link className={styles.link} to="#">
               
        </Link>
      </div>
    </form>
  );
};

export default EmailForm;
